import React from 'react';
import PropTypes from 'prop-types';

export class WeatherSelect extends React.Component {
    constructor(props, context) {
        super(props, context);

        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(e) {
        const { onSelect } = this.props;
        onSelect && onSelect(e.target.value);
    }

    render() {

        const { title } = this.props;

        return (
            <select
                className="header-select"
                value={title}
                onChange={this.handleChange}>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>

                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>

                <option value=""></option>
                <option value=""></option>
                <option value=""></option>

                <option value=""></option>
                <option value=""></option>
                <option value=""></option>

                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>

                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
            </select>
        )
    }
}

WeatherSelect.propTypes = {
    title: PropTypes.string.isRequired,
    onSelect: PropTypes.func.isRequired
};


